
*{
    box-sizing: border-box;
}

body {
    font-family: "inter";
    margin: 0;
    min-height: 100vh;
    background-color: #282D35;
    display: flex;
    flex-direction: column;
}

nav {
    display: flex;
    align-items: center;
    background-color: #21222A;
    height: 90px;
    padding: 20px 30px;
}

.navbar-logo {
    width: 35px;
    margin-right: 10px;
}
.navbar-logo:hover {
    filter: invert(30%);
}

.navbar-logo-text {
    margin: 0;
    margin-right: auto;
    color: #61DAFB;
    font-size: 22px;
}

.main-facts > li:hover {
    color: #00b7ff;
}

.navbar-title {
    margin: 0;
    color: #DEEBF8;
    font-weight: 600;
}

main {
    padding: 20px 20px;
    color: white;
    line-height: 19px;
    background-position: right 60%;
    display: flex;
    flex-direction: column;
    background-image: url(./comp/images/logo-half.png);
    background-repeat: no-repeat;
    background-size: 20%;
}

.main-title {
    margin-left: 16px;
    font-size: 39px;
    letter-spacing: -0.05em;
    line-height: 1.2;
}

.main-facts > li {
    line-height: 2;
    padding-block: 10px;
    max-width: 400px;
}

.main-facts > li::marker {
    font-size: 1.4rem;
    color: #61DAFB;
}

footer {
    color: white;
    font-size: 11px;
    font-weight: 300;
    text-align: center;
    padding-block: 12px;
    margin-top: auto;
}

.footer-icons {
    width: 23px;
    filter: invert(100%);
    margin-right: 13px;
    margin-bottom: 3px;
}

.footer-icons:hover{
    filter: invert(30%);
}

footer > p {
    line-height: .5;
}

@media screen and (min-width: 500px) {
    nav {
        padding: 30px 38px;
    }
    
    main {
        padding: 30px 28px;
        background-size: 10%;
    }

    footer {
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
    }
}

